import React from 'react';
import { useRouter, usePopout } from '@unexp/router'
import { Alert, Panel, PanelHeaderBack, PanelHeader, View, Group, Header, HorizontalCell, Card, CardGrid, ContentCard, HorizontalScroll, Div, Button } from '@vkontakte/vkui';
import { Icon20LikeCircleFillRed, Icon20MoneyCircleOutline, Icon20ArticleBoxOutline, Icon20DonateCircleFillYellow } from '@vkontakte/icons';
import axios from 'axios';
import Status from './Status';
import img_first from '../img/img_first.jpg'; // Картинка для товара "Первое"
import img_apart from '../img/img_apart.jpg'; // Картинка для товара "Отдельное"



const largeImageStyles = { // Стиль
	width: 220,
	height: 124,
	borderRadius: 8,
	border: '1px solid var(--placeholder_icon_background)',
	objectFit: 'cover'
};

const Shop = ({ id, properties, API_KEY }) => {
	let { push, back, replace } = useRouter();
	let { setPopout, closePopout } = usePopout();

	/* ============ Блок с ценами ============*/
	/* Баллы */
	let point_mixed = properties.price[0].points.mixed;
	let point_first = properties.price[0].points.first;
	let point_apart = properties.price[0].points.apart;

	/* Рубли */
	let rub_mixed = properties.price[0].rub.mixed;
	let rub_first = properties.price[0].rub.first;
	let rub_apart = properties.price[0].rub.apart;
	// let rub_securing = properties.price[0].rub.securing;

	/* Функция продажи LT за баллы */
	async function marketPoint(api, id, price, position) {
		await axios.get(`https://likebot.ru/marketPoint?token=${api}&id=${id}&price=${price}&position=${position}`)
			.then(async function (response) {
				if (response.status === 204) { // Если баллов нет
					return setPopout(
						<Alert
							actions={[
								{ title: 'Как получить баллы?', action: () => replace({ view: "info", panel: "info" }), mode: 'destructive' },
								{ title: 'Понятно', autoclose: true, mode: 'cancel' },
							]}
							actionsLayout='vertical'
							onClose={closePopout}
							header='Отказано!'
							text='У Вас недостаточно баллов, подкопите и повторите попытку'
						/>
					)
				}
				console.log(response.status); // Status ответа
				console.log(response.data); // Что отвечает сервер
				setPopout(
					<Alert
						actions={[
							{ title: 'Супер!', mode: 'cancel', autoclose: true },
						]}
						actionsLayout='vertical'
						onClose={closePopout}
						header='Успех'
						text='Вы успешно купили ЛТ, скоро Вы будете на стеночке'
					/>
				)
			})
			.catch(function (err) {
				console.log("ERROR SYKa BLYAT: ", err)
			})
	}

	/* Функция продажи LT за баллы */
	async function marketRub(api, id, price, position) {
		await axios.get(`https://likebot.ru/marketRub?token=${api}&id=${id}&price=${price}&position=${position}`)
			.then(async function (response) {
				if (response.status === 204) { // Если рублей нет
					return setPopout(
						<Alert
							actions={[
								{ title: 'Пополнить', action: () => replace({ view: "info", panel: "info" }), mode: 'destructive' },
								{ title: 'Отмена', autoclose: true, mode: 'cancel' },
							]}
							actionsLayout='vertical'
							onClose={closePopout}
							header='Отказано!'
							text='Вам необходимо пополнить счёт'
						/>
					)
				}
				console.log(response.status); // Status ответа
				console.log(response.data); // Что отвечает сервер
				setPopout(
					<Alert
						actions={[
							{ title: 'Супер!', mode: 'cancel', autoclose: true },
						]}
						actionsLayout='vertical'
						onClose={closePopout}
						header='Успех'
						text='Вы успешно купили ЛТ, скоро Вы будете на стеночке'
					/>
				)
			})
			.catch(function (err) {
				console.log("ERROR SYKa BLYAT: ", err)
			})
	}

	return (

		<View activePanel={id}>

			<Panel id="shop">
				<PanelHeader left={<PanelHeaderBack onClick={back} />}>
					Магазин
        </PanelHeader>
				<Status properties={properties}></Status>
				<Group header={<Header aside={<Icon20LikeCircleFillRed />}>Like Time</Header>} mode="card">
					<HorizontalScroll>
						<div style={{ display: 'flex' }}>
							<HorizontalCell onClick={() => push({ panel: 'apart' })} size='l' header='Отдельное' subtitle={`${rub_apart} рублей или ${rub_apart} баллов`}>
								<img alt="lt" style={largeImageStyles}
									src={img_apart} />
							</HorizontalCell>
							<HorizontalCell onClick={() => push({ panel: 'first' })} size='l' header='Первое' subtitle={`${rub_first} рублей или ${point_first} баллов`}>
								<img alt="lt" style={largeImageStyles}
									src={img_first} />
							</HorizontalCell>
							<HorizontalCell onClick={() => push({ panel: 'mixed' })} size='l' header='Рандомное' subtitle={`${rub_mixed} рублей или ${point_mixed} баллов`}>
								<img alt="lt" style={largeImageStyles}
									src={img_first} />
							</HorizontalCell>
						</div>
					</HorizontalScroll>
				</Group>
			</Panel>

			<Panel id="apart">
				<PanelHeader left={<PanelHeaderBack onClick={back} />}>
					Товар
</PanelHeader>
				<Group info="Information for user">
					<Header aside={<Icon20ArticleBoxOutline />}>Описание</Header>
					<CardGrid size="l">
						<Card>
							<ContentCard
								image={img_first}
								header="Отдельное ЛТ"
								text="Вы будете опубликованы Отдельное в посту"
								caption="Лайки идут к Вам на страничку"
								mode="outline"
								maxHeight={240}
							/>
						</Card>
					</CardGrid>
				</Group>
				<Status properties={properties}></Status>
				<Group>
					<Group mode="card">
						<Header>Купить</Header>
						<Div style={{ display: 'flex' }}>
							<Button onClick={async () => { (marketRub(API_KEY, properties.vk, rub_apart, "apart")) }} before={<Icon20MoneyCircleOutline />} size="l" stretched mode="commerce">За {rub_apart} рублей</Button>
						</Div>
						<Div style={{ display: 'flex' }}>
							<Button onClick={async () => { (marketPoint(API_KEY, properties.vk, point_apart, "apart")) }} before={<Icon20DonateCircleFillYellow />} size="l" stretched mode="tertiary">За {point_apart} баллов</Button>
						</Div>
					</Group>
				</Group>
			</Panel>

			<Panel id="first">
				<PanelHeader left={<PanelHeaderBack onClick={back} />}>
					Товар
</PanelHeader>
				<Group info="Information for user">
					<Header aside={<Icon20ArticleBoxOutline />}>Описание</Header>
					<CardGrid size="l">
						<Card>
							<ContentCard
								image={img_first}
								header="Первое ЛТ"
								text="Вы будете опубликованы Первым в посту"
								caption="Лайки идут к Вам на страничку"
								mode="outline"
								maxHeight={240}
							/>
						</Card>
					</CardGrid>
				</Group>
				<Status properties={properties}></Status>
				<Group>
					<Group mode="card">
						<Header>Купить</Header>
						<Div style={{ display: 'flex' }}>
							<Button onClick={async () => { (marketRub(API_KEY, properties.vk, rub_first, "first")) }} before={<Icon20MoneyCircleOutline />} size="l" stretched mode="commerce">За {rub_first} рублей</Button>
						</Div>
						<Div style={{ display: 'flex' }}>
							<Button onClick={async () => { (marketPoint(API_KEY, properties.vk, point_first, "first")) }} before={<Icon20DonateCircleFillYellow />} size="l" stretched mode="tertiary">За {point_first} баллов</Button>
						</Div>
					</Group>
				</Group>
			</Panel>

			<Panel id="mixed">
				<PanelHeader left={<PanelHeaderBack onClick={back} />}>
					Товар
</PanelHeader>
				<Group info="Information for user">
					<Header aside={<Icon20ArticleBoxOutline />}>Описание</Header>
					<CardGrid size="l">
						<Card>
							<ContentCard
								image={img_first}
								header="Рандомное ЛТ"
								text="Вы будете опубликованы Рандомным в посту"
								caption="Лайки идут к Вам на страничку если люди ставят руками"
								mode="outline"
								maxHeight={240}
							/>
						</Card>
					</CardGrid>
				</Group>
				<Status properties={properties}></Status>
				<Group>
					<Group mode="card">
						<Header>Купить</Header>
						<Div style={{ display: 'flex' }}>
							<Button onClick={async () => { (marketRub(API_KEY, properties.vk, rub_mixed, "mixed")) }} before={<Icon20MoneyCircleOutline />} size="l" stretched mode="commerce">За {rub_mixed} рублей</Button>
						</Div>
						<Div style={{ display: 'flex' }}>
							<Button onClick={async () => { (marketPoint(API_KEY, properties.vk, point_mixed, "mixed")) }} before={<Icon20DonateCircleFillYellow />} size="l" stretched mode="tertiary">За {point_mixed} баллов</Button>
						</Div>
					</Group>
				</Group>
			</Panel>



		</View>

	)
};

/* 
					<Button onClick={async () => { (balanceChanger(API_KEY, properties.vk, properties.balance, change, "give")) }} size="l" mode="commerce"/*<- Делает зеленой before={<Icon28MarketAddBadgeOutline />} stretched /*<- растягивает >Купить</Button>
*/

export default Shop;
